.Card {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
  margin-bottom: 15px;
  border-radius: 20px;
}

.Card_white {
  background-color: #fff;
  -webkit-box-shadow: 0 10px 10px -5px rgb(194,201,201, 0.4);
          box-shadow: 0 10px 10px -5px rgb(194,201,201, 0.4)
}

.Card_green {
  background-color: #00e29b;
  -webkit-box-shadow: 0 10px 10px -5px rgb(194,201,201, 0.4);
          box-shadow: 0 10px 10px -5px rgb(194,201,201, 0.4);
  color: #fff;
}

.Card__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
}

.Card__checkbox {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 50px;
  width: 50px;
  margin-right: 20px;
  /* background-color: #c2c9c9; */
  background-color: transparent;
  border-radius: 25px;
  -webkit-box-shadow: inset 0 0 0 2px #c2c9c9;
          box-shadow: inset 0 0 0 2px #c2c9c9;
  text-align: center;
  color: #c2c9c9;
  font-weight: 700;
  font-size: 1rem;

  -webkit-transition: background-color 0.3s ease, -webkit-box-shadow 0.8s ease;

  transition: background-color 0.3s ease, -webkit-box-shadow 0.8s ease;

  -o-transition: background-color 0.3s ease, box-shadow 0.8s ease;

  transition: background-color 0.3s ease, box-shadow 0.8s ease;

  transition: background-color 0.3s ease, box-shadow 0.8s ease, -webkit-box-shadow 0.8s ease
}

.Card.is-enabled .Card__checkbox {
  background-color: #00e29b;
  -webkit-box-shadow: inset 0 0 0 0 transparent;
          box-shadow: inset 0 0 0 0 transparent;
  color: #fff;
}

.Card__title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #c2c9c9;
}

.Card.is-enabled .Card__title {
  color: #003ec8;
}

.Card__description {
  color: #c2c9c9;
  line-height: 1.25;
}



